<template>
  <el-container>
    <el-header>Header</el-header>
    <el-container>
      <el-aside width="200px">
        <el-row>
          <el-menu default-active="/home/courseprogress" :router="true">
            <el-menu-item index="/home/courseprogress">
              <span slot="title">课程进度</span>
            </el-menu-item>
            <el-menu-item index="/home/courses">
              <span slot="title">课程管理</span>
            </el-menu-item>
            <el-menu-item index="/home/users">
              <span slot="title">人员管理</span>
            </el-menu-item>
            <el-menu-item index="/home/majors">
              <span slot="title">专业管理</span>
            </el-menu-item>
          </el-menu>
        </el-row>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {};
  }
};
</script>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
}

body > .el-container {
  margin-bottom: 5px;
}
</style>


