<template>
  <div>
    <el-button type="primary" @click="dialogVisible=true">新增专业</el-button>

    <el-table :data="majorData" stripe border style="width: 100%" v-loading="loading">
      <el-table-column prop="id" label="id" width="180px" align="center" />
      <el-table-column prop="name" label="专业名" width="180px" align="center" />
      <el-table-column label="操作" width="500px" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="editDialogVisible=true; currentMajorInit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteMajor(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="editDialogVisible" width="30%">
      <el-form :model="currentMajor">
        <el-form-item label="专业名" prop="name">
          <el-input v-model="currentMajor.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogVisible=false">取消</el-button>
        <el-button @click="editMajor" type="primary">确定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="dialogVisible" width="30%">
      <el-form :model="newMajor">
        <el-form-item label="专业名" prop="name">
          <el-input v-model="newMajor.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="dialogVisible=false">取消</el-button>
        <el-button @click="addMajor" type="primary">确定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      majorData: [],
      editDialogVisible: false,
      dialogVisible: false,
      currentMajor: {
        id: null,
        name: null
      },
      newMajor: {
        id: null,
        name: null
      },
      loading: true
    };
  },
  methods: {
    dataInit() {
      this.loading = true;
      this.majorData = null;
      axios
        .get("http://localhost:8080/major")
        .then(res => {
          this.majorData = res.data.data;
          this.loading = false;
        })
        .catch(err => {
          console.error(err);
        });
    },
    currentMajorInit(index, rowData) {
      this.currentMajor.name = rowData.name;
      this.currentMajor.id = rowData.id;
    },
    editMajor() {
      let data = {
        id: this.currentMajor.id,
        name: this.currentMajor.name
      };
      axios({
        url: "http://localhost:8080/major",
        method: "put",
        data: qs.stringify(data)
      })
        .then(res => {
          this.dataInit();
        })
        .catch(err => {
          console.log(err);
        });
      this.editDialogVisible = false;
    },
    deleteMajor(id) {
      this.$confirm("是否删除专业", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          axios
            .delete("http://localhost:8080/major/" + id)
            .then(res => {
              this.dataInit();
              this.$message({ type: "success", message: "删除成功!" });
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(err => {
          this.$message({ type: "info", message: "已取消删除" });
          console.log(err);
        });
    },
    addMajor() {
      let data = {
        name: this.newMajor.name
      };
      axios
        .post("http://localhost:8080/major/", qs.stringify(data))
        .then(res => {
          this.dataInit();
        })
        .catch(err => {
          console.log(err);
        });
      this.dialogVisible = false;
      this.newMajor.name = null;
    }
  },
  mounted() {
    this.dataInit();
  }
};
</script>

<style>
</style>


