<template>
  <div>
    <el-button type="primary" @click="AddDialogVisible = true">新增人员</el-button>

    <el-table :data="userData" stripe border v-loading="loading" style="width: 100%">
      <el-table-column prop="id" label="id" width="180"></el-table-column>

      <el-table-column prop="username" label="姓名" width="180"></el-table-column>

      <el-table-column prop="role" label="身份" width="180"></el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="userEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="userDeleteConfirm(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="EditDialogVisible" width="400px">
      <el-form>
        <el-form-item label="成员名">
          <el-input v-model="currentUser.username"></el-input>
        </el-form-item>
        <el-form-item label="职能分配">
          <el-select placeholder="请选择职能" v-model="currentUser.roleId">
            <el-option
              v-for="item in roleList"
              :key="item.value"
              :value="item.value"
              :label="item.label"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="userUpdate">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="AddDialogVisible" width="400px">
      <el-form label-width="70px" :rules="rules" :model="newUser">
        <el-form-item label="成员名" prop="username">
          <el-input v-model="newUser.username" />
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="newUser.email" />
        </el-form-item>
        <el-form-item label="职能" prop="role">
          <el-select placeholder="请选择职能" v-model="newUser.roleId">
            <el-option label="教研" value="4" />
            <el-option label="讲师" value="2" />
            <el-option label="剪辑" value="3" />
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AddDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="userAdd">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import qs from "qs";
import axios from "axios";
export default {
  data() {
    return {
      roleList: [
        { value: 4, label: "教研" },
        { value: 2, label: "讲师" },
        { value: 3, label: "剪辑" }
      ],
      userData: [],
      adminname: "",
      adminId: 1,
      EditDialogVisible: false,
      AddDialogVisible: false,
      loading: false,
      currentUser: {
        id: null,
        username: "",
        roleId: null,
        email: "",
        password: "",
        adminId: null
      },
      newUser: { username: "", roleId: null, email: "", adminId: null },
      rules: {
        username: [
          { required: true, message: "请输入用户名！", trigger: "blur" }
        ],
        email: [
          { required: true, message: "请输邮箱！", trigger: "blur" },
          { type: "email", message: "请输入正确的邮箱格式！", trigger: "blur" }
        ],
        role: [{ required: true, message: "请选择职能！", trigger: "blur" }]
      }
    };
  },

  methods: {
    userEdit(index, rowData) {
      this.EditDialogVisible = true;
      this.currentUser.roleId = parseInt(this.userData[index].roleId);
      this.currentUser.id = rowData.id;
      this.currentUser.username = rowData.username;
      this.currentUser.adminId = 1;
    },
    userUpdate() {
      let roleData = {
        uid: this.currentUser.id,
        rid: this.currentUser.roleId
      };
      let userData = {
        username: this.currentUser.username,
        id: this.currentUser.id
      };
      axios
        .all([
          axios.post("http://localhost:8080/user_role", qs.stringify(roleData)),
          axios.put("http://localhost:8080/users", qs.stringify(userData))
        ])
        .then(
          axios.spread((res1, res2) => {
            this.EditDialogVisible = false;
            this.dataInit();
          })
        )
        .catch(err => {
          console.error(err);
        });
    },
    userDeleteConfirm(index, rowData) {
      this.$confirm("是否删除成员?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          axios
            .delete("http://localhost:8080/users/" + rowData.username)
            .then(res => {
              this.$message({ type: "success", message: "删除成功!" });
              this.dataInit();
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    userAdd() {
      let data = {
        username: this.newUser.username,
        email: this.newUser.email,
        adminId: 1,
        roleId: this.newUser.roleId
      };
      axios({
        url: "http://localhost:8080/users/",
        method: "post",
        headers: { "content-type": "application/json" },
        data: JSON.stringify(data)
      })
        .then(res => {
          this.AddDialogVisible = false;
          this.dataInit();
        })
        .catch(err => {
          console.error(err);
        });
    },
    dataInit() {
      this.loading = true;
      this.userData.length = 0;
      axios
        .get("http://localhost:8080/users/groupbyadminid/" + this.adminId)
        .then(res => {
          res.data.data.forEach(element => {
            this.userData.push({
              id: element.id,
              username: element.username,
              role: element.roles[0].nameZh,
              roleId: element.roles[0].id
            });
          });
          this.loading = false;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  created() {
    this.dataInit();
  },
  mounted() {}
};
</script>

<style>
</style>


