<template>
  <el-card class="elCard">
    <div slot="header">
      <span class="coursename">{{courseName}}</span>
      <el-button type="primary" class="elCardButton" @click="toKnowledegPointList(courseId, courseName)">考点详情</el-button>
    </div>
    <div :id="courseId" class="catalogChart"></div>
  </el-card>
</template>

<script>
import axios from "axios";
import echarts from "echarts";
export default {
  props: {
    courseId: {
      type: Number
    },
    courseName:{
      type:String
    }
  },
  data() {
    return {
      catalogChart: null,
    };
  },
  methods: {
    dataInit() {
      axios
        .get("http://localhost:8080/courses/" + this.courseId + "/progress")
        .then(res => {
          let progressdata = res.data.data;
          let data = [];
          //插入顺序决定表格显示顺序
          data.push(progressdata.knowledgePointsSumCount);
          data.push(progressdata.videoUploadCompleteCount);
          data.push(progressdata.videoFinalizationCompleteCount);
          data.push(progressdata.videoFirstDraftCompleteCount);
          data.push(progressdata.pptFinalizationCompleteCount);
          data.push(progressdata.pptFirstDraftCompleteCount);          
          let catalogChartOption = {
            color: ["#3398DB"],
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "shadow"
              }
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true
            },
            xAxis: [
              {
                type: "category",
                data: [
                  "PPT初稿",
                  "PPT定稿",
                  "视频初稿",
                  "视频定稿",
                  "视频上传",
                  "总量"
                ],
                axisTick: {
                  alignWithLabel: true
                }
              }
            ],
            yAxis: [
              {
                type: "value"
              }
            ],
            series: [
              {
                name: "总量",
                type: "bar",
                barWidth: "60%",
                data: JSON.parse(JSON.stringify(data.reverse()))
              }
            ]
          };
          this.catalogChart = echarts.init(document.getElementById(this.courseId));
          this.catalogChart.setOption(catalogChartOption);
        })
        .catch(err => {
          console.error(err);
        });
    },
    toKnowledegPointList(courseId, courseName) {
      this.$router.push({ path: "courses/" + courseId, query:{courseName:courseName} });
    }
  },
  mounted() {
    
  },
  created(){
    this.dataInit();
  }
};
</script>

<style>
.catalogChart {
  height: 250px;
  width: 500px;
}
.elCard {
  width: 97%;
  height: 350px;
}
.elCardButton {
  float: right;
}
.coursename {
  font-size: x-large;
  font-weight: 900;
}
</style>


