<template>
  <div>
    <el-button type="success"><strong>{{courseName}}专业</strong></el-button>
    <el-button type="primary" plain @click="currentKnowledegPointClear" icon="el-icon-circle-plus">新增知识点</el-button>
    
    <el-table :data="knowledegPointsData" stripe border v-loading="loading" style="width: 100%">
      <el-table-column prop="id" label="id" width="40px" align="center" />
      <el-table-column prop="chapter" label="章" width="40px" align="center" />
      <el-table-column prop="section" label="节" width="40px" align="center" />
      <el-table-column prop="spot" label="点" width="40px" align="center" />
      <el-table-column prop="name" label="点名称" width="200px" />
      <el-table-column prop="pptFirstDraftAt" label="PPT初稿" width="100px" align="center">
        <template slot-scope="scope">
          <v-commit
            v-if="scope.row.pptFirstDraftAt == null"
            :knowledegPointId="scope.row.id"
            fieldName="pptFirstDraftAt"
            @tableDataInit="knowledegPointsDataInit"
            previousProcess="firstDefauft"
            :role="user.role" 
          ></v-commit>
          <el-popover trigger="hover" placement="right" v-if="scope.row.pptFirstDraftAt != null">
            <p>完成时间: {{ scope.row.pptFirstDraftAt }}</p>
            <div slot="reference">
              <el-tag size="medium" type="success">已完成</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="pptFinalizationAt" label="PPT定稿" width="100px" align="center">
        <template slot-scope="scope">
          <v-commit
            v-if="scope.row.pptFinalizationAt == null"
            :knowledegPointId="scope.row.id"
            fieldName="pptFinalizationAt"
            @tableDataInit="knowledegPointsDataInit"
            :previousProcess="scope.row.pptFirstDraftAt"
          ></v-commit>
          <el-popover trigger="hover" placement="right" v-if="scope.row.pptFinalizationAt != null">
            <p>完成时间: {{ scope.row.pptFinalizationAt }}</p>
            <div slot="reference">
              <el-tag size="medium" type="success">已完成</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="videoFirstDraftAt" label="视频初稿" width="100px" align="center">
        <template slot-scope="scope">
          <v-commit
            v-if="scope.row.videoFirstDraftAt == null"
            :knowledegPointId="scope.row.id"
            fieldName="videoFirstDraftAt"
            @tableDataInit="knowledegPointsDataInit"
            :previousProcess="scope.row.pptFinalizationAt"
          ></v-commit>
          <el-popover trigger="hover" placement="right" v-if="scope.row.videoFirstDraftAt != null">
            <p>完成时间: {{ scope.row.videoFirstDraftAt }}</p>
            <div slot="reference">
              <el-tag size="medium" type="success">已完成</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="videoFinalizationAt" label="视频定稿" width="100px" align="center">
        <template slot-scope="scope">
          <v-commit
            v-if="scope.row.videoFinalizationAt == null"
            :knowledegPointId="scope.row.id"
            fieldName="videoFinalizationAt"
            @tableDataInit="knowledegPointsDataInit"
            :previousProcess="scope.row.videoFirstDraftAt"
          ></v-commit>
          <el-popover
            trigger="hover"
            placement="right"
            v-if="scope.row.videoFinalizationAt != null"
          >
            <p>完成时间: {{ scope.row.videoFinalizationAt }}</p>
            <div slot="reference">
              <el-tag size="medium" type="success">已完成</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="videoUploadAt" label="视频上传" width="100px" align="center">
        <template slot-scope="scope">
          <v-commit
            v-if="scope.row.videoUploadAt == null"
            :knowledegPointId="scope.row.id"
            fieldName="videoUploadAt"
            @tableDataInit="knowledegPointsDataInit"
            :previousProcess="scope.row.videoFinalizationAt"
          ></v-commit>
          <el-popover trigger="hover" placement="right" v-if="scope.row.videoUploadAt != null">
            <p>完成时间: {{ scope.row.videoUploadAt }}</p>
            <div slot="reference">
              <el-tag size="medium" type="success">已完成</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="teacherName" label="讲师" width="70px" align="center" />
      <el-table-column prop="teacherEditorName" label="教研" width="70px" align="center" />
      <el-table-column prop="videoEditorName" label="剪辑" width="70px" align="center" />
      <el-table-column label="操作" width="160px" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="knowledegPointEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="knowledegPointDeleteConfirm(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="editDialogVisible" width="35%">
      <el-form
        label-position="right"
        label-width="15%"
        :rules="rules"
        :model="currentKnowledegPoint"
      >
        <el-form-item label="考点名" prop="name">
          <el-input v-model="currentKnowledegPoint.name"></el-input>
        </el-form-item>
        <el-form-item label="章" prop="chapter">
          <el-input v-model.number="currentKnowledegPoint.chapter"></el-input>
        </el-form-item>
        <el-form-item label="节" prop="section">
          <el-input v-model.number="currentKnowledegPoint.section"></el-input>
        </el-form-item>
        <el-form-item label="点" prop="spot">
          <el-input v-model.number="currentKnowledegPoint.spot"></el-input>
        </el-form-item>
        <el-form-item label="讲师">
          <el-select v-model="currentKnowledegPoint.teacherId">
            <el-option
              v-for="teacher in courseTeacherList"
              :label="teacher.name"
              :value="teacher.id"
              :key="teacher.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="教研">
          <el-select v-model="currentKnowledegPoint.teacherEditorId">
            <el-option
              v-for="teacheEditor in courseTeacherEditorList"
              :label="teacheEditor.name"
              :value="teacheEditor.id"
              :key="teacheEditor.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="剪辑">
          <el-select v-model="currentKnowledegPoint.videoEditorId">
            <el-option
              v-for="videoEditor in courseVideoEditorList"
              :label="videoEditor.name"
              :value="videoEditor.id"
              :key="videoEditor.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="postOrPutKnowledegPoint">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import CommitKnowledegPointTabel from "./CommitKnowledegPointTabel";
export default {
  components: {
    "v-commit": CommitKnowledegPointTabel
  },
  data() {
    return {
      //从localsatge全局获取用户
      user:{
        id:17,
        name:"离子键",
        role:"讲师"
      },
      //动态路由传参获取课程id
      courseId: null,
      courseName:null,
      knowledegPointsData: [],
      loading: true,
      addDialogVisible: false,
      editDialogVisible: false,
      rules: {
        name: [{ required: true, message: "请输入考点名！", trigger: "blur" }],
        chapter: [
          { required: true, message: "请输入章", trigger: "blur" },
          { type: "number", message: "必须为阿拉伯数字！", trigger: "blur" }
        ],
        section: [
          { required: true, message: "请输入节！", trigger: "blur" },
          { type: "number", message: "必须为阿拉伯数字！", trigger: "blur" }
        ],
        spot: [
          { required: true, message: "请输入点！", trigger: "blur" },
          { type: "number", message: "必须为阿拉伯数字！", trigger: "blur" }
        ]
      },
      currentKnowledegPoint: {
        id: null,
        courseId: null,
        chapter: null,
        section: null,
        spot: null,
        name: "",
        pptFirstDraftAt: "",
        pptFinalizationAt: "",
        videoFirstDraftAt: "",
        videoFinalizationAt: "",
        videoUploadAt: "",
        teacher: "",
        teacherEditor: "",
        videoEditor: "",
        teacherId: 0,
        teacherEditorId: 0,
        videoEditorId: 0
      },
      courseTeacherList: [],
      courseTeacherEditorList: [],
      courseVideoEditorList: [],
    };
  },
  methods: {
    knowledegPointEdit(index, rowdata) {
      this.editDialogVisible = true;
      this.currentKnowledegPoint.id = rowdata.id;
      this.currentKnowledegPoint.name = rowdata.name;
      this.currentKnowledegPoint.chapter = rowdata.chapter;
      this.currentKnowledegPoint.section = rowdata.section;
      this.currentKnowledegPoint.spot = rowdata.spot;
      this.currentKnowledegPoint.teacher = this.knowledegPointsData[
        index
      ].teacherName;
      this.currentKnowledegPoint.teacherEditor = this.knowledegPointsData[
        index
      ].teacherEditorName;
      this.currentKnowledegPoint.videoEditor = this.knowledegPointsData[
        index
      ].videoEditorName;
      this.currentKnowledegPoint.teacherId = this.knowledegPointsData[
        index
      ].teacherId;
      (this.currentKnowledegPoint.teacherEditorId = this.knowledegPointsData[
        index
      ].teacherEditorId),
        (this.currentKnowledegPoint.videoEditorId = this.knowledegPointsData[
          index
        ].videoEditorId);
    },
    knowledegPointDeleteConfirm(index, rowData) {
      this.$confirm("是否删除知识点？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          axios
            .delete("http://localhost:8080/knowledgepoints/" + rowData.id)
            .then(res => {
              this.knowledegPointsData.splice(index, 1);
            })
            .catch(err => {
              console.error(err);
            });
          this.$message({ type: "success", message: "删除成功!" });
        })
        .catch(() => {
          this.$message({ type: "info", message: "已取消删除！" });
        });
    },
    currentKnowledegPointClear() {
      this.editDialogVisible = true;
      this.currentKnowledegPoint.id = 0;
      this.currentKnowledegPoint.name = null;
      this.currentKnowledegPoint.chapter = null;
      this.currentKnowledegPoint.section = null;
      this.currentKnowledegPoint.spot = null;
      this.currentKnowledegPoint.teacher = null;
      this.currentKnowledegPoint.teacherEditor = null;
      this.currentKnowledegPoint.videoEditor = null;
      this.currentKnowledegPoint.teacherId = null;
      this.currentKnowledegPoint.teacherEditorId = null;
      this.currentKnowledegPoint.videoEditorId = null;
    },
    postOrPutKnowledegPoint() {
      let data = {
        courseId: this.courseId,
        chapter: this.currentKnowledegPoint.chapter,
        section: this.currentKnowledegPoint.section,
        spot: this.currentKnowledegPoint.spot,
        name: this.currentKnowledegPoint.name,
        teacherId: this.currentKnowledegPoint.teacherId,
        teacherEditorId: this.currentKnowledegPoint.teacherEditorId,
        videoEditorId: this.currentKnowledegPoint.videoEditorId
      };
      if (this.currentKnowledegPoint.id == 0) {
        axios
          .post("http://localhost:8080/knowledgepoints", qs.stringify(data))
          .then(res => {
            this.knowledegPointsDataInit();
          })
          .catch(err => {
            console.error(err);
          });
      } else {
        axios
          .put(
            "http://localhost:8080/knowledgepoints/" +
              this.currentKnowledegPoint.id,
            qs.stringify(data)
          )
          .then(res => {
            this.knowledegPointsDataInit();
          })
          .catch(err => {
            console.error(err);
          });
      }
      this.editDialogVisible = false;
    },
    knowledegPointsDataInit() {
      this.loading = true;
      axios
        .get(
          "http://localhost:8080/knowledgepoints/getbycourse/" + this.courseId
        )
        .then(res => {
          this.knowledegPointsData = res.data.data;
          this.loading = false;
        })
        .catch(err => {
          console.error(err);
        });
    },
    userListInit() {
      axios
        .get("http://localhost:8080/users", {
          params: { courseId: this.courseId }
        })
        .then(res => {
          res.data.data.forEach(element => {
            if (element.roles[0].nameZh == "讲师") {
              this.courseTeacherList.push({
                id: element.id,
                name: element.username
              });
            } else if (element.roles[0].nameZh == "教研") {
              this.courseTeacherEditorList.push({
                id: element.id,
                name: element.username
              });
            } else if (element.roles[0].nameZh == "剪辑") {
              this.courseVideoEditorList.push({
                id: element.id,
                name: element.username
              });
            }
          });
        })
        .catch(err => {
          console.error(err);
        });
    },
    commitProgressAuthority(){

    }
  },
  created() {
  },
  mounted() {
    this.courseName = this.$route.query.courseName;
    this.courseId = this.$route.params.id;
    this.knowledegPointsDataInit();
    this.userListInit();
  }
};
</script>

<style>
</style>


