<template>
  <div >
    <el-row v-loading="loading">
      <el-col :span="24">
        <CourseProgressCard v-for="item in courseList" :key="item.id" :courseId="item.id" :courseName="item.courseName"></CourseProgressCard>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
import CourseProgressCard from "./CourseProgressCard";
export default {
  data() {
    return {
      loading:true,
      courseList:[],
      adminId:1
    };
  },
  methods:{
    dataInit(){
      this.loading = true
      axios.get('http://localhost:8080/courses/groupbyuserid/'+this.adminId)
      .then(res => {
        this.courseList = res.data.data;
        this.loading = false
      })
      .catch(err => {
        console.error(err); 
      })
    }
  },
  mounted(){
    this.dataInit();
  },
  components: {
    CourseProgressCard
  }
};
</script>

<style>
</style>


