<template>
  <el-form ref="userForm" :model="user" label-width="30%">
    <el-form-item label="用户名">
      <el-input v-model="user.username"></el-input>
    </el-form-item>
    <el-form-item label="密码">
      <el-input v-model="user.password"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button @click="submitLogin">登陆</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      user: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    submitLogin() {
      console.log(this.user);
      axios({
        url: "http://localhost:8080/login",
        method: "post",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        data: qs.stringify(this.user)
      })
        .then(res => {
          if (res.data.status == 0) {
            this.$router.push({
              path: '/home/courses',
            });
          }
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
};
</script>

<style>
</style>


