<template>
  <div>
    <el-button type="primary" @click="currentCourseClear">新增课程</el-button>

    <el-table
      :data="courseData"
      :stripe="true"
      :border="true"
      style="width: 100%"
      v-loading="loading"
      element-loading-text="拼命加载中"
    >
      <el-table-column prop="id" label="id" width="180px" align="center" />
      <el-table-column prop="majorName" label="所属专业" width="180px" align="center" />
      <el-table-column prop="courseName" label="课程名" width="180px" align="center" />
      <el-table-column label="考点详情" width="180px" align="center">
        <template slot-scope="scope">
          <el-button type="success" size="mini" @click="toKnowledegPointList(scope.row.id, scope.row.courseName)">查看</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="500px" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="teacherDialogVisible=true; setCurrentCourseData(scope.$index, scope.row)"
          >关联讲师</el-button>
          <el-button
            size="mini"
            @click="teacherEditorDialogVisible=true; setCurrentCourseData(scope.$index, scope.row)"
          >关联教研</el-button>
          <el-button
            size="mini"
            @click="videoEditorDialogVisible=true; setCurrentCourseData(scope.$index, scope.row)"
          >关联剪辑</el-button>
          <el-button size="mini" @click="editCourse(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteCourse(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="editDialogVisible" width="400px">
      <el-form :model="currentCourse">
        <el-form-item label="课程名" v-model="currentCourse.courseName">
          <el-input v-model="currentCourse.courseName"></el-input>
        </el-form-item>
        <el-form-item label="所属专业">
          <el-select v-model="currentCourse.majorId" placeholder="请选择专业">
            <el-option
              v-for="item in allMajorList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitEditCourse">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="addDialogVisible" width="400px">
      <el-form :model="currentCourse">
        <el-form-item label="课程名" v-model="currentCourse.courseName">
          <el-input v-model="currentCourse.courseName"></el-input>
        </el-form-item>
        <el-form-item label="所属专业">
          <el-select v-model="currentCourse.majorId" placeholder="请选择专业">
            <el-option
              v-for="item in allMajorList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCourse">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="teacherDialogVisible" width="600px">
      <el-transfer v-model="hasSelectedTeachers" :data="AllTeacherList" :titles="['备选讲师', '已选讲师']" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="teacherDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUserCourseRelation">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="teacherEditorDialogVisible" width="600px">
      <el-transfer
        v-model="hasSelectedTeacherEditors"
        :data="AllTeacherEditorList"
        :titles="['备选教研', '已选教研']"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="teacherEditorDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUserCourseRelation">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog :visible.sync="videoEditorDialogVisible" width="600px">
      <el-transfer
        v-model="hasSelectedVideoEditors"
        :data="AllVideoEditor"
        :titles="['备选剪辑', '已选剪辑']"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="videoEditorDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUserCourseRelation">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      loading: true,
      adminId: 1,

      AllTeacherList: [],
      AllTeacherEditorList: [],
      AllVideoEditor: [],
      courseData: [],
      teacherDialogVisible: false,
      teacherEditorDialogVisible: false,
      videoEditorDialogVisible: false,

      editDialogVisible: false,
      addDialogVisible: false,

      hasSelectedTeachers: [],
      hasSelectedTeacherEditors: [],
      hasSelectedVideoEditors: [],

      allMajorList: [],

      currentCourse: {
        id: "",
        courseName: "",
        majorId: null,
        majorName: "",
        teacherList: [],
        teacherEditorList: [],
        videoEditorList: [],
        progress: null,
        adminId: null
      }
    };
  },
  methods: {
    editCourse: function(index, rowData) {
      this.editDialogVisible = true;
      this.currentCourse.id = rowData.id;
      this.currentCourse.courseName = rowData.courseName;
      this.currentCourse.majorName = rowData.majorName;
      this.currentCourse.majorId = this.courseData[index].majorId;
    },
    submitEditCourse() {
      let data = {
        courseName: this.currentCourse.courseName,
        majorId: this.currentCourse.majorId
      };
      axios
        .put("http://localhost:8080/courses/" + this.currentCourse.id, data)
        .then(res => {
          this.dataInit();
        })
        .catch(err => {
          console.error(err);
        });
      this.editDialogVisible = false;
    },
    dataInit() {
      this.currentCourse.teacherList = [];
      this.currentCourse.teacherEditorList = [];
      this.currentCourse.videoEditorList = [];
      this.loading = true;
      this.courseData = null;
      axios
        .get("http://localhost:8080/courses/groupbyuserid/" + this.adminId)
        .then(res => {
          this.courseData = res.data.data;
          this.loading = false;
        })
        .catch(err => {
          console.error(err);
        });
    },
    deleteCourse(index, rowData) {
      this.$confirm("是否删除课程？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          axios
            .delete("http://localhost:8080/courses/" + rowData.id)
            .then(res => {
              this.dataInit();
              this.$message({ type: "success", message: "删除成功!" });
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(() => {
          this.$message({ type: "info", message: "已取消删除！" });
        });
    },
    addCourse() {
      let data = {
        courseName: this.currentCourse.courseName,
        majorId: this.currentCourse.majorId,
        adminId: 1
      };
      axios
        .post("http://localhost:8080/courses", qs.stringify(data))
        .then(res => {
          this.addDialogVisible = false;
          this.dataInit();
        })
        .catch(err => {
          console.error(err);
        });
    },
    currentCourseClear() {
      this.addDialogVisible = true;
      this.currentCourse.id = null;
      this.currentCourse.courseName = null;
      this.currentCourse.majorId = null;
      this.currentCourse.majorName = null;
    },
    setCurrentCourseData(index, rowData) {
      this.currentCourse.id = this.courseData[index].id;
      
      this.courseData[index].userList.forEach(element => {
        if (element.roles[0].nameZh == "教研") {
          this.currentCourse.teacherEditorList.push(element);
        } else if (element.roles[0].nameZh == "讲师") {
          this.currentCourse.teacherList.push(element);
        } else if (element.roles[0].nameZh == "剪辑") {
          this.currentCourse.videoEditorList.push(element);
        }
      });
      // this.currentCourse.teacherList = this.courseData[index].teacherList;
      // this.currentCourse.teacherEditorList = this.courseData[index].teacherEditorList;
      // this.currentCourse.videoEditorList = this.courseData[index].videoEditorList;
      
      this.hasSelectedTeachers = [];
      this.hasSelectedTeacherEditors = [];
      this.hasSelectedVideoEditors = [];
      
      this.currentCourse.teacherList.forEach(element => {
        this.hasSelectedTeachers.push(element.id);
      });
      this.currentCourse.teacherEditorList.forEach(element => {
        this.hasSelectedTeacherEditors.push(element.id);
      });
      this.currentCourse.videoEditorList.forEach(element => {
        this.hasSelectedVideoEditors.push(element.id);
      });

    },
    addUserCourseRelation() {
      let userlist = this.hasSelectedTeachers.concat(
        this.hasSelectedTeacherEditors,
        this.hasSelectedVideoEditors
      );
      axios
        .post(
          "http://localhost:8080/courses/" +
            this.currentCourse.id +
            "/userrelation",
          userlist
        )
        .then(res => {
          this.teacherDialogVisible = false;
          this.teacherEditorDialogVisible = false;
          this.videoEditorDialogVisible = false;
          this.dataInit();
        })
        .catch(err => {
          console.error(err);
        });
    },
    userlistDataInit() {
      this.AllTeacherEditorList.length = 0;
      this.AllTeacherList.length = 0;
      this.AllVideoEditor.length = 0;
      axios
        .get("http://localhost:8080/users/groupbyadminid/" + this.adminId)
        .then(res => {
          let userlist = res.data.data;
          userlist.forEach(element => {
            if (element.roles[0].nameZh == "教研") {
              this.AllTeacherEditorList.push({
                key: element.id,
                label: element.username,
                disable: false
              });
            } else if (element.roles[0].nameZh == "讲师") {
              this.AllTeacherList.push({
                key: element.id,
                label: element.username,
                disable: false
              });
            } else if (element.roles[0].nameZh == "剪辑") {
              this.AllVideoEditor.push({
                key: element.id,
                label: element.username,
                disable: false
              });
            }
          });
        })
        .catch(err => {
          console.error(err);
        });
    },
    allMajorListInit() {
      axios
        .get("http://localhost:8080/major")
        .then(res => {
          this.allMajorList = res.data.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    toKnowledegPointList(courseId, courseName) {
      this.$router.push({ path: "courses/" + courseId, query:{courseName:courseName} });
    }
  },
  mounted() {
    this.dataInit();
    this.userlistDataInit();
    this.allMajorListInit();
  }
};
</script>

<style>
</style>


