<template>
  <div>
    <el-button type="danger" plain v-popover:commitProgress size="mini" @click="initTime">未完成</el-button>
    <el-popover ref="commitProgress" width="295" trigger="click" placement="right" >
      <el-date-picker
        v-model="time"
        type="datetime"
        placeholder="选择日期时间"
        align="center"
        value-format="yyyy-MM-dd HH:mm:ss"
      />
      <el-button size="large" type="success" @click="commitProgress" :disabled="buttonVisible" >完成</el-button>
    </el-popover>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      time: "",
      buttonVisible:true
    };
  },
  props: {
    knowledegPointId: Number,
    fieldName: String,
    previousProcess:null,
    role:String
  },
  methods: {
    commitProgress() {
      let data = {};
      let name = this.fieldName;
      data[name] = this.time;
      axios
        .put(
          "http://localhost:8080/knowledgepoints/" + this.knowledegPointId,
          qs.stringify(data)
        )
        .then(res => {
          //触发父组件数据刷新
          this.$emit("tableDataInit");
        })
        .catch(err => {
          console.error(err);
        });
    },
    initTime() {
      const nowtime = new Date();
      function dateFormat(fmt, date) {
        var o = {
          "M+": date.getMonth() + 1, //月份
          "d+": date.getDate(), //日
          "H+": date.getHours(), //小时
          "m+": date.getMinutes(), //分
          "s+": date.getSeconds(), //秒
          "q+": Math.floor((date.getMonth() + 3) / 3), //季度
          S: date.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt))
          fmt = fmt.replace(
            RegExp.$1,
            (date.getFullYear() + "").substr(4 - RegExp.$1.length)
          );
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(
              RegExp.$1,
              RegExp.$1.length == 1
                ? o[k]
                : ("00" + o[k]).substr(("" + o[k]).length)
            );
        return fmt;
      }
      this.time = dateFormat("yyyy-MM-dd HH:mm:ss", nowtime);
      this.submissionAuthority();
    },
    submissionAuthority(){
      if (this.previousProcess != null) {
        this.buttonVisible = false;
      }
    }
  }
};
</script>

<style>
</style>


